<template>
	<div class="example-container">
		<template v-if="ctype == 'crumb'">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item v-for="(item,index) in breadList" :key="index" :to="{ path: item.path }">
					{{item.meta.title+'-'+item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</template>
		<template v-else-if="ctype == 'tag'">
			<el-row :gutter="24"  style="padding: 10px;align-items: center;" type="flex" justify="start">
				<el-col :span="1"><i class="fa el-icon-s-home fa-2x" style="color:#909399;margin-right: 10px;"></i></el-col>
				<el-col :span="21">
					<el-tag  :key="index" v-for="(tag,index) in dynamicTags" :type="index==idx?'':'info'" closable :disable-transitions="false"
						@close="handleClose(tag.meta.title,index)" @click="toLink(tag.path)" size="medium" style="margin-right: 10px;">
						<!-- <router-link :to="tag.path">{{tag.meta.title}}</router-link> -->
						{{tag.meta.title}}
					</el-tag>
				</el-col>
				<el-col :span="2">
					<el-dropdown @command="handleCommand">
					  <span class="el-dropdown-link">
					    其他选项<i class="el-icon-arrow-down el-icon--right"></i>
					  </span>
					  <el-dropdown-menu slot="dropdown">
					    <el-dropdown-item command="a">关闭左侧</el-dropdown-item>
					    <el-dropdown-item command="b">关闭右侧</el-dropdown-item>
					    <el-dropdown-item command="c">全部关闭</el-dropdown-item>
					  </el-dropdown-menu>
					</el-dropdown>
				</el-col>
			</el-row>
		</template>
	</div>
</template>
<script>
	export default {
		name: 'Breadcrumb',
		data() {
			return {
				breadList: [], // 路由集合
				dynamicTags: [],
				idx:0
			};
		},
		props:{
			ctype: {
			    type: String,
			    default() {
			        return 'crumb';
			    }
			},
			
		},
		watch: {
			$route() {
				this.getBreadcrumb();
			}
		},
		methods: {
			isHome(route) {
				return route.name === "Index" || route.name === "Main";
			},
			getBreadcrumb() {
				let matched = this.$route.matched;
				console.log(matched[0]);
				let a = matched[matched.length - 1];
				let idx = this.dynamicTags.findIndex(item => item.meta.title == a.meta.title)
				if(idx == -1){
					this.dynamicTags = [...this.dynamicTags, a]
					this.idx = this.dynamicTags.length-1;
				}else{
					this.idx = idx;
				}
				
				//如果不是首页
				if (!this.isHome(matched[0])) {
					matched = [{
						path: "/index",
						meta: {
							title: "首页"
						}
					}].concat(matched);
				}
				this.breadList = matched;
			},
			handleClose(tag,idx) {
				let len = this.dynamicTags.length;
				this.dynamicTags.splice(this.dynamicTags.findIndex(item => item.meta.title == tag), 1)
				this.$router.push(this.dynamicTags[this.dynamicTags.length-1].path);
				if(idx < len-1){
					this.idx = this.dynamicTags.length-1;
				}
			},
			toLink(path){
				this.$router.push(path)
			},
			handleCommand(command) {
			    if(command == 'a'){
					this.dynamicTags.splice(0,this.idx);
					this.idx = 0;
				}else if(command == 'b'){
					let len = this.dynamicTags.length;
					this.dynamicTags.splice(this.idx+1,len-1-this.idx);
					this.idx = this.dynamicTags.length-1;
				}else if(command == 'c'){
					this.dynamicTags = [];
				}
			}
		},
		created() {
			this.getBreadcrumb();
		}
		
	
	};
</script>
